.Soon {
  position: relative;
  top: 0;
  width: 100vw;
  min-height: 100vh;
  display: flex;
  background-color: #000000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 1s cubic-bezier(0.76, 0, 0.24, 1);
  z-index: 9999;
}
.plusesAnim {
  position: absolute;
  height: 150vh;
  width: 150%;
  overflow: hidden;
  top: -50vh;
  // top: 0vh;
  left: -50vw;
  // left: 0vw;
  transition: all 1s linear;
}
.plusesInfBlock {
  position: absolute;
  top: 100%;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  // transition: all 1s linear;
  color: #fff;
  // animation-fill-mode: forwards;
}
@keyframes moveRightBottom {
  0% {
    transform: translateY(-35%) translateX(-10%);
  }
  50% {
    transform: translateY(-100%) translateX(-60%);
  }
  100% {
    transform: translateY(-35%) translateX(-10%);
  }
}
.plusesAnim .plus {
  width: max-content;
  height: max-content;
  position: relative;
  color: white;
  width: 10vh;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: calc(0.75vh + 0.75vw);
}
.plusesAnim .plus span {
  transition: all 0.35s linear;
  transform: rotate(180deg);
  transform-origin: center;
  transform-origin: center center;
  text-align: center;
}
