.SoonText {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f5eded;
  font-size: 64px;
  font-family: "LOMBOK_REGULAR";
  font-weight: 400;
  z-index: 102;
  transition: opacity 0.5s cubic-bezier(0.76, 0, 0.24, 1);
}

.canvasSnow2 {
  position: absolute;
  width: 100%;
  height: 100vh;
  object-fit: cover;
  z-index: 101;
  top: 0vh;
  transition: opacity 3.5s ease-out;
}
